// U1	
export const newUser = (work_no, username, password, email) => {
  // console.log( work_no, username,password,email);
  return new axios({
    url: baseUrl + "/users",
    data: {
      work_no: work_no,
      username: username,
      password: password,
      email: email
    },
    method: POST
  });
};

// U2	
export const Login = (work_no, password) => {
  return new axios({
    url: baseUrl + "/login",
    data: {
      work_no: work_no,
      password: password
    },
    method: POST
  });
};

// U3	
export const getUser = () => {
  return new axios({
    url: baseUrl + "/users/current",
    method: GET
  });
};

// U4	
export const Logout = () => {
  return new axios({
    url: baseUrl + "/users/logout",
    method: POST
  });
};

// 

// S1	
export const Suggestions = (type, keyword, size) => {
  return new axios({
    url:
      baseUrl +
      "/search/suggestions?type=" +
      type +
      "&keyword=" +
      keyword +
      "&size=" +
      size,
    method: GET
  });
};

// S2	
export const Associations = (keyword, tag_count, category_count) => {
  return new axios({
    url:
      baseUrl +
      "/search/associations?keyword=" +
      keyword +
      "&tag_count=" +
      tag_count +
      "&category_count=" +
      category_count,
    method: GET
  });
};

// S3	
export const Results = (
  type,
  keyword,
  tags,
  categories,
  exts,
  created_time,
  modified_time,
  time_zone,
  page,
  per_page
) => {
  // console.log(type, keyword, tags, categories, exts, created_time, modified_time, time_zone, page, per_page);
  return new axios({
    url: baseUrl + "/search/results",
    data: {
      type: type,
      keyword: keyword,
      tags: tags,
      categories: categories,
      exts: exts,
      created_time: created_time,
      modified_time: modified_time,
      time_zone: time_zone,
      page: page,
      per_page: per_page,
      desc_highlight_count: 2,
      content_highlight_count: 5,
      highlight_tag: "em"
    },
    method: POST
  });
};

// S4	
export const Tags = (keyword, size) => {
  return new axios({
    url: baseUrl + "/search/tags?keyword=" + keyword + "&size=" + size,
    method: GET
  });
};
export const Categories = (keyword, size) => {
  return new axios({
    url: baseUrl + "/search/categories?keyword=" + keyword + "&size=" + size,
    method: GET
  });
};

